export default {
	props: {
		item: Object,
		index: Number
	},
	data() {
		return {
			isguanZhu: this.item.isguanZhu,
			InfoNumIndex: this.item.infoNum.index,
			dingNum: this.item.infoNum.dingNum,
			caiNum: this.item.infoNum.caiNum
		};
	},
	methods: {
		changeIsguanZhu() {
			if (!this.isguanZhu) {
				this.isguanZhu = true;
				uni.showToast({
					title: '关注成功'
				});
			} else {
				this.isguanZhu = false;
				uni.showToast({
					title: '取消关注'
				});
			}
		},
		checkImg(idx){
			uni.previewImage({
				current:idx,
				 urls: this.item.morePic,
				 indicator:"default"
			})
			
		}
	}
};
